// src/components/Table/Table.scss
.custom-table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
    font-size: 1rem;
    text-align: left;
  
    th,
    td {
      border: 1px solid #ddd;
      padding: 8px;
    }
  
    th {
      background-color: #f4f4f4;
      font-weight: bold;
    }
  
    tr:nth-child(even) {
      background-color: #f9f9f9;
    }
  
    button {
      margin-right: 5px;
      padding: 5px 10px;
      cursor: pointer;
    }


    .no-data {
      text-align: center;
      padding: 20px;
      width: 100%;
      
    }
  }